var paymentId = window.location.search.substring(1);
var paymentNumber = paymentId.split('=');
var paymentWindow = document.getElementById("payment_window");
var key = "qdvqyeh5x4xlpecwtmk2h22v";
// var css = "http:%2F%2Flocalhost:3000/%2Fcheckout%2Fcheckout-window.css";
var css = "https:%2F%2Fcorpsite.qa.qualys.com/%2Fcheckout%2Fcheckout-window.css";
var logo = "https:%2F%2Fwww.qualys.com%2Fasset%2Fimage%2Flogo%2Fqualys-shield.svg";

if( paymentNumber[0] && paymentNumber[1] ){

	var paramId;
	var moduleType;
	if( paymentNumber[0] === "cid" ){
		paramId = "extCustomerId";
		moduleType = "ns_openInvoices";
	}
	else if( paymentNumber[0] === "iid" ){
		paramId = "extInvoiceId";
		moduleType = "ns_Invoice";
	}

	if( paramId !== ""){
		var paystandUrl = "https://checkout.paystand.co/v4/?publishableKey="+ key +"&viewCheckout=checkout_payment&module="+ moduleType +"&"+ paramId +"="+ paymentNumber[1] +"&externalCss="+ css +"&logoUrl="+ logo;
		paymentWindow.setAttribute("src", paystandUrl);
	}

}